.footer {
  width: 100%;

  max-width: 1366px;
  min-height: 75px;
  margin: 0 auto;
  margin-top: auto;

  border-top: 2px solid $border-input;
}


.footer__info {
  display: flex;
  justify-content: space-between;

  margin-top: 25px;
  padding: 0 20px;

  & ul {
    display: flex;
    justify-content: space-between;
    min-width: 70px;
  }

  & a {
    &:hover {
      opacity: 0.5;
    }

    &:focus {
      opacity: 0.5;
    }

    &:active {
      opacity: 0.3;
    }
  }

  & .footer__insta,
  .footer__fb {
    width: 30px;
    height: 30px;

    opacity: 0.5;
  }
}

.footer__address {
  & p {
    margin-top: 0;
    font-size: 15px;
    line-height: 15px;
  }
}

@media (max-width: $width-mobile) {
  .footer__address {
    & .footer__text {
      display: none;
    }

    & p {
      font-size: 12px;
      line-height: 14px;
      width: 55%;
    }
  }

  .footer__info {
    margin-top: 2.5vh;
    padding: 0;
  }
}
