.main-nav {
  width: 65%;
  max-width: 700px;
  font-family: $font-title;

  & ul {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 4%;

    font-size: 22px;
  }

  & li {
    padding-right: 7%;
  }

  & a {
    font-size: 25px;
    color: $text-dark;
    line-height: 32px;
    font-weight: 700;

    & :hover {
      color: $title;
      opacity: 0.9;
    }

    & :focus {
      color: $title;
      opacity: 0.9;
    }

    & :active {
      color: $title;
      opacity: 0.5;
    }
  }

  & .active {
    border-bottom: 2px solid $title;
  }
}

@media (max-width: $tablet-width) {
  .main-nav {
    max-width: 100%;

    position: fixed;
    right: -100%;
    top: 0;
    width: 170px;
    height: 100%;
    background-image: linear-gradient(to left, #82dbf1 0%, #f5f7fa 50%, rgba(255, 255, 255, 0.8) 100%);
    margin: 0;
    transition: right 0.4s;
    display: flex;
    flex-direction: column;
    padding-top: 110px;

    & ul {
      justify-content: start;
    }

    & li {
      margin-bottom: 20px;
      width: 100%;
    }
  }
}
