.scroll__btn {
  margin-top: auto;
  margin-bottom: 15px;
  width: 20px;
}

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;

  user-select: none;
  touch-action: pan-y;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  &:focus {
    outline: none;
  }

  &.dragging {
    cursor: hand;
  }
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  overflow: hidden;
  display: flex;
  justify-content: center;
  min-width: 0;
  width: 100%;
}


.slick-slide {
  float: left;
  height: 80%;
  min-height: 1px;
  max-height: 685px;

  [dir="rtl"] & {
    float: right;
  }

  &.slick-loading img {
    display: none;
  }

  display: none;

  &.dragging img {
    pointer-events: none;
  }

  .slick-initialized & {
    display: block;
  }

  .slick-loading & {
    visibility: hidden;
  }

  .slick-vertical & {
    display: block;
    height: auto;
    border: 1px solid transparent;
  }
}

.slick-slider button {
  font-size: 0;
  line-height: 0;

  border: none;
  outline: none;

  display: block;
  text-decoration: none;
  position: absolute;
  top: 60%;
  left: 35%;
  transform: translate(-60%, -35%);

  padding: 3px;
  border-radius: 50%;
  background-color: $title;
  opacity: 0.2;
  transition: background-color 0.2s ease;
}

.slick-dots {
  display: flex;
}

.slick-dots > li {
  display: flex;
  justify-content: center;
  position: relative;
  width: 10px;
  height: 15px;

  cursor: pointer;
  text-align: center;
  user-select: none;
  margin: 0 2px;
}

.slick-slider .slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 7px;
}

.slick-dots > .slick-active > button {
  opacity: 1;
  height: 8px;
  width: 8px;
  background: $title;
}


@media (max-width: $tablet-width) {
  .slick-slider {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}


@media (max-width: $width-mobile) {
  .slick-slider {
    justify-content: flex-start;
  }

  .slick-slide {
    max-height: 90vw;
  }
}
