.contact {
  & h2 {
    @include title;

    margin-bottom: 10px;
  }
}

.contact__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;

  & .contact__map {
    width: 48%;
    height: auto;
  }


  & textarea {
    border-radius: 10px;
    outline: none;
    border: 1px solid #479393;
    padding: 10px;
  }

  & .contact__message {
    display: flex;
    flex-direction: column;
  }

  & .contact__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 48%;

    font-size: 20px;
    line-height: 25px;
    margin-bottom: 20px;

    & p {
      font-size: 18px;
    }
  }

  & .contact__form {
    margin-top: 20px;
    width: 100%;
  }

  & .contact__info {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 10px;
    padding: 10px 0;
  }

  & .contact__name,
  .contact__email {
    font-size: 17px;
    width: 49%;
    display: flex;
    justify-content: space-between;
  }

  & input {
    padding: 10px 7px;
    width: 75%;

    border-radius: 5px;
    font-weight: 500;

    outline: none;
    border: 1px solid #479393;

    &::placeholder {
      font-size: 11px;
    }
  }

  & .contact__checkbox {
    display: flex;
  }

  & button {
    display: block;
    padding: 10px 30px;
    margin: 10px auto;
    border-radius: 5px;

    font-family: $font-basic;
    font-size: 20px;
    line-height: 24px;
    color: white;

    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
    outline: none;
    border: none;
    cursor: pointer;

    &:focus {
      opacity: 0.9;
    }

    &:hover {
      opacity: 0.9;
    }

    &:active {
      opacity: 0.7;
    }
  }
}

.contact__message {
  & label {
    font-size: 17px;
    margin-bottom: 25px;
  }
}

.contact__checkbox {
  & input {
    width: 20px;
    margin-right: 15px;
  }
}

.contact__map-block {
  position: relative;
  display: flex;
  flex-wrap: wrap;

  min-height: 385px;
}

.contact__iframe {
  z-index: 1;
  width: 100%;

  border: none;
}

.contact__image {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

  z-index: 0;
  height: 363px;
}


@media (max-width: $tablet-width) {
  .contact__block {
    padding: 0;

    & input {
      max-width: 230px;
      min-width: 200px;
      margin-left: 10px;
    }

    & .contact__name,
    .contact__email {
      margin-bottom: 10px;
      width: 100%;
    }

    & .contact__info {
      flex-direction: column;
      padding: 0;
      margin: 0;
    }

    & .contact__wrapper {
      width: 40%;
    }

    & .contact__map {
      width: 53%;
      height: auto;
    }
  }
}


@media (max-width: $width-mobile) {
  .contact {
    & h2 {
      font-size: 40px;
      margin-bottom: 0;
      // font-size: 5vh;

      &::before {
        content: none;
      }

      &::after {
        content: none;
      }
    }

    & .contact__name,
    .contact__email {
      font-size: 15px;
      justify-content: flex-start;
    }

    & input {
      &::placeholder {
        font-size: 10px;
      }
    }
  }

  .contact__map {
    display: none;
  }

  .contact__form {
    & .btn {
      font-size: 0.8em;
      padding: 7px 30px;
    }
  }

  .contact__block {
    & .contact__wrapper {
      margin-bottom: 1vh;
      width: 100%;

      & p {
        font-size: 15px;
        line-height: 17px;
        margin-bottom: 0;
      }
    }
  }

  .contact__message {
    & label {
      font-size: 15px;
      line-height: 17px;
      margin-bottom: 1vh;
    }

    & textarea {
      height: 30vw;
      min-height: 100px;
    }
  }
}
