.gallery__list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  padding: 0 20px;
  margin: 0;
  margin-bottom: 25px;
  margin-top: 5%;

  width: 100%;

  & li:nth-child(4n) {
    margin-right: 0;
  }

  & .gallery__link {
    display: flex;
    flex-direction: column;
    text-align: center;
    height: 400px;
    user-select: text;
  }
}

.gallery__item {
  width: 23%;
  height: auto;
  margin-right: 2.65%;
  margin-bottom: 45px;

  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 3px;

  & p {
    margin: 5px 0;
  }
}

.gallery__img {
  height: 70%;
  width: 100%;

  & img {
    width: auto;
    height: 100%;
    object-fit: contain;

    cursor: zoom-in;
    border-radius: 3px;
    padding: 0 10px;
    padding-top: 5px;
  }
}

.gallery__details {
  color: $text-dark;
  font-size: 14px;
  width: 70%;
  align-self: center;

  & td {
    padding: 0;
    margin: 0;
    text-align: start;
    width: 50%;
    border-collapse: collapse;
  }
}

@media (max-width: $tablet-width) {
  .gallery__list {
    & li:nth-child(4n) {
      margin-right: 4.7%;
    }

    & li:nth-child(3n) {
      margin-right: 0;
    }
  }

  .gallery__item {
    width: 30%;
    margin-right: 4.7%;

    & p {
      font-size: 15px;
    }
  }

  .gallery__details {
    font-size: 12px;
  }
}

@media (max-width: $width-mobile) {
  .gallery__list {
    margin-top: 40px;

    & li:nth-child(4n) {
      margin-right: 0;
    }

    & .gallery__link {
      height: 400px;
    }
  }

  .gallery__item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 30px;

    & p {
      font-size: 14px;
      margin-bottom: 0;
    }
  }

  .gallery__img {
    height: 78%;
  }

  .gallery__details {
    font-size: 12px;
    max-width: 200px;
    margin-bottom: 5px;
  }
}
