.btn {
  padding: 10px 30px;
  margin: 10px auto;
  border-radius: 5px;

  font-family: $font-basic;
  font-size: 1em;
  line-height: 22px;
  color: white;
  background-color: $title;

  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
  outline: none;
  border: none;
  cursor: pointer;

  &:focus {
    opacity: 0.9;
  }

  &:hover {
    opacity: 0.9;
  }

  &:active {
    opacity: 0.7;
  }
}
