.intro {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  text-align: center;
  width: 100%;
  padding: 0 20px;

  & h2 {
    position: relative;
    z-index: 5;

    @include title;

    font-size: 45px;
    line-height: 45px;
    font-family: $font-nothing;
    font-weight: bold;
    margin-bottom: 30px;

    &::before {
      content: none;
    }

    &::after {
      content: none;
    }
  }

  & p {
    font-size: 1em;
    line-height: 1.3em;
  }
}

.intro__welcome {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  & img {
    display: inline-block;
    margin: 0 auto;
  }
}

.intro__block {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  & img {
    display: inline-block;
    margin: 0 auto;
    width: 90%;
    max-width: 510px;
    height: auto;
  }
}

.intro__list {
  position: relative;
  box-sizing: border-box;
  display: block;
  overflow: hidden;
  height: auto;
}

.intro__item {
  display: block;
  overflow: hidden;
  height: auto;
  min-width: 0;
  padding: 0;

  outline: none;
}

@media (max-width: $tablet-width) {
  .intro {
    justify-content: flex-start;
    flex-direction: column;

    & h2 {
      font-size: 40px;
      margin-bottom: 2vw;
      margin-top: 3vw;
    }

    & p {
      font-size: 18px;
      line-height: 25px;
    }
  }

  .intro__list {
    height: 100%;
  }

  .intro__welcome {
    width: 100%;
    height: auto;

    & img {
      display: none;
    }

    & p {
      display: none;
    }

    & a {
      display: none;
    }
  }

  .intro__block {
    width: 100%;
    height: auto;
    justify-content: center;

    & img {
      display: inline-block;
      margin: 0 auto;
      width: 50vh;
      max-width: none;
      height: auto;
    }
  }
}

@media (max-width: $width-mobile) {
  .intro {
    & h2 {
      font-size: 5vh;
      line-height: 5vh;
      margin-bottom: 2vh;
      margin-top: 20px;
    }
  }

  .intro__welcome {
    & img {
      display: none;
    }
  }

  .intro__block {
    width: 100%;
    height: auto;

    & img {
      display: inline-block;
      margin: 0 auto;
      width: 60vw;
      max-width: none;
      height: auto;
    }
  }

  .intro__slider {
    max-height: none;
  }
}
