.about__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 20px;
  width: 100%;

  & h2 {
    @include title;
  }

  & p {
    font-size: 20px;
    line-height: 30px;

    margin-bottom: 5.5px;
    text-align: center;

    margin-right: 20px;
    margin-left: 20px;
    width: 100%;
  }
}

.about__photo {
  height: auto;
  margin: 0 auto;

  display: flex;
  flex-direction: column;

  & .about__img {
    position: relative;
    display: inline-block;

    width: 100%;
    height: auto;
  }

  & .about__img::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 0 5px 5px rgb(216, 212, 212) inset;
  }

  & figcaption {
    font-size: 20px;
    text-align: center;
    margin-top: 20px;
  }
}

.about__text {
  position: relative;
  width: 100%;

  &::before {
    position: absolute;
    content: " ";
    display: inline-block;
    background-color: $title;
    height: 2px;
    width: 50%;
    transform: translateX(-50%);

    left: 50%;
    bottom: -15px;
    vertical-align: middle;
    flex-grow: 0;
    flex-shrink: 0;
  }
}


@media (max-width: $tablet-width) {
  .about__block {
    & p {
      font-size: 18px;
      line-height: 30px;

      margin-bottom: 5.5px;
      text-align: center;

      margin-right: 20px;
      margin-left: 20px;
      width: 100%;
    }
  }

  .about__photo {
    & figcaption {
      font-size: 16px;
    }

    & .about__img {
      width: 70vw;
      height: auto;
    }
  }
}

@media (max-width: $width-mobile) {
  .about__block {
    padding: 0;
    width: 100%;

    & p {
      font-size: 2.3vh;
      line-height: 3vh;
      min-width: 300px;
      margin-right: 0;
      margin-left: 0;
    }

    & h2 {
      font-size: 5vh;
      margin-bottom: 0.5%;

      &::before {
        content: none;
      }

      &::after {
        content: none;
      }
    }
  }

  .about__photo {
    display: none;

    & figcaption {
      font-size: 16px;
    }

    & .about__img {
      width: 70vw;
      height: auto;
    }
  }
}
