.page-header {
  position: fixed;
  width: 100%;

  top: 0;
  left: 0;
  z-index: 5;
  background-image: linear-gradient(to bottom, #82dbf1 0%, #f5f7fa 80%, transparent 100%);
}

.page-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.page-header__block {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
}

.page-header__toggle {
  display: none;
}

.page-header__logo {
  padding: 20px 0;

  & img {
    width: 200px;
  }
}

@media (max-width: $tablet-width) {
  .page-header {
    min-height: auto;
  }

  .page-header__block {
    flex-direction: column;

    min-height: auto;
    width: 95%;
  }

  .page-header__logo {
    display: flex;
    justify-content: space-between;

    width: 95%;
    z-index: 5;
  }

  .page-header::before {
    z-index: 4;
  }

  .page-header__toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 45px;
    margin-left: auto;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;

    & span {
      position: relative;
      display: inline-block;
      width: 30px;
      height: 2px;
      background-color: $title-dark;
      transition: background 0.3s 0.5s;
    }
  }

  .page-header__toggle > span::after,
  .page-header__toggle > span::before {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: $title-dark;
    transform-origin: 50% 50%;
    transition: top 0.3s 0.3s, transform 0.3s;
  }

  .page-header__toggle > span::after {
    top: 10px;
  }

  .page-header__toggle > span::before {
    top: -10px;
  }

  .page-header--close .page-header__toggle > span::before {
    transform: rotate3d(0, 0, 1, 45deg);
    top: 0;
    transition: top 0.3s, transform 0.2s 0.3s;
  }

  .page-header--close .page-header__toggle > span::after {
    transform: rotate3d(0, 0, 1, -45deg);
    top: 0;
    transition: top 0.3s, transform 0.2s 0.3s;
  }

  .page-header--close .page-header__toggle > span {
    background: 0 0;
    transition: background 0.1s 0.1s;
  }


  .page-header--close .main-nav {
    display: block;
    right: 0;
  }
}
